@use '../../../styles/mixins';

.myWaresWrapper {
  padding: 16px 0 32px 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  justify-content: center;
}

.waresList {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.wareItem {
    padding-bottom: 8px;
    border: 1px solid var(--border-color);
    display: flex;
    flex-direction: column;
    gap: 8px;
    &:hover{
        border: 1px solid var(--accent-color);
    }

  @include mixins.mq(tablet) {
    padding-bottom: 0;
    width: 100%;
    height: 400px;
    flex-direction: row;
    justify-content: space-between;
    gap: 12px;
  }
}

.box {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
  justify-content: center;
  @include mixins.mq(desktop) {
    flex-direction: row;
    gap: 30px;
    justify-content: space-around;
  }
}

.descriptionWrapper {
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  justify-content: center;

  @include mixins.mq(tablet) {
    justify-content: flex-start;
  }
  @include mixins.mq(desktop) {
    width: 600px;
  }
}

.descriptionThumb {
    font-size: 12px;
    color: var(--text-color);
    overflow: auto;
    /* Стилізація самого скрола */
    &::-webkit-scrollbar {
        width: 6px;
        background-color: var(--bg-footer-header);
    }
    /* Стилізація повзунка */
    &::-webkit-scrollbar-thumb {
        background-color: var(--background-btn-main-color);
        width: 6px;
        border-radius: 9em;
    }
        /* Стилізація полоси прокрутки при наведенні */
    &::-webkit-scrollbar-thumb:hover {
        background-color: var(--btn-border-color); /* Колір повзунка при наведенні */
    }
    
    @include mixins.mq(tablet) {
        height: 200px;
    }
}

.buttonWrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  @include mixins.mq(tablet) {
    gap: 12px;
  }

  @include mixins.mq(desktop) {
    width: 150px;
    flex-direction: column;
    padding: 0 8px;
    gap: 20px;
  }
}
